import {RouterModule} from '@angular/router'

import {
  LineaListaComponent,
  ProductoListasMainComponent,
  ProductosListaComponent,
  PresentacionesListaComponent
} from './'

const routes = [{
  path: '',
  component: ProductoListasMainComponent,
  children: [{
    path: 'lineas',
    component: LineaListaComponent
  }, {
    path: 'lineas/:linea/productos',
    component: ProductosListaComponent
  }, {
    path: 'lineas/:linea/productos/:producto/presentaciones',
    component: PresentacionesListaComponent
  }]
}]

export const ListaRoutes = RouterModule.forChild(routes)



