import {NgModule} from '@angular/core'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'

import {
  ProductoListasMainComponent,
  LineaListaComponent,
  ProductosListaComponent,
  PresentacionesListaComponent,
  ListaRoutes
} from './'

import {WidgetsModule} from '../../../widgets'

@NgModule({
  imports: [
    FormsModule,
    CommonModule,
    ListaRoutes,
    WidgetsModule
  ],
  declarations: [
    ProductoListasMainComponent,
    LineaListaComponent,
    ProductosListaComponent,
    PresentacionesListaComponent
  ]
})
export class ListaProductosModule {
  public static routes = ListaRoutes
}



