import { Component } from '@angular/core'
import {Router, ActivatedRoute, Params} from '@angular/router'
import { Observable } from 'rxjs/Observable'
import 'rxjs/add/operator/switchMap'

import {ProductoService, AuthService} from '../../../../services'

declare const $:any

@Component({
  moduleId: module.id,
  templateUrl: 'presentaciones.component.html',
  styleUrls: ['presentaciones.component.scss']
})
export class PresentacionesListaComponent {
  presentaciones$: Observable<any>
  user$: Observable<any>
  presentacion$: Observable<any>

  productoId: string
  lineaId: string

  constructor(
    private productoService: ProductoService,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ){
      route.params
        .subscribe((params: Params) => {
          this.productoId = params['producto']
          this.lineaId = params['linea']
          return this.getPresentaciones()
        })

    this.user$ = authService.USER
    this.presentaciones$.subscribe(l => console.log(l))
  }

  getPresentaciones() {
    this.presentaciones$ = this.productoService
    .getPresentaciones(this.productoId, this.lineaId)
  }

  edit(codigo) {
    this.router.navigate(['', 'admin', 'registrar', 'productos', 'editar', 'linea' ,this.lineaId, 'producto', this.productoId, 'presentacion', codigo])
  }

  loadPresentaciones(codigo) {
    this.presentacion$ = this.productoService.getPresentacion(this.lineaId, this.productoId, codigo)
    this.presentacion$.subscribe(d => console.log(d))
    $('#presentacionDetails').modal('show')
  }

  deletePresentacion (element){
    this.productoService
      .deletePresentacion(this.lineaId, this.productoId, element.codigo)
      .subscribe(
        r => this.getPresentaciones(),
        err => console.log(err),
        () => console.log('deleting presentacion process finished.')
      )
  }
}



