import { Component } from '@angular/core'
import {Router, ActivatedRoute} from '@angular/router'
import {Observable} from 'rxjs/Observable'
import {Subject} from 'rxjs/Subject'
import {ProductoService, AuthService} from '../../../../services'

// TODO: import models
import {ProductoFiltros} from '../../../../models'

import 'rxjs/add/operator/share'
import 'rxjs/add/operator/debounceTime'
import 'rxjs/add/operator/distinctUntilChanged'
import 'rxjs/add/operator/startWith'

declare const $:any

@Component({
  moduleId: module.id,
  templateUrl: 'linea.component.html',
  styleUrls: ['linea.component.scss']
})
export class LineaListaComponent {
  lineas$: Observable<any>
  user$: Observable<any>
  filtro$ = new Subject<any>()
  filter: ProductoFiltros
  filtered: boolean = false
  filtered$: Observable<any>

  linea: string

  constructor(
    private productoService: ProductoService,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ){
    this.lineas$ = productoService.getLineas()
    this.user$ = authService.USER
  }

  ngOnInit() {
    this.filter = {
      limit: 25,
      product: '',
      total: 0,
      skip: 0
    }

    this.filtro$
        .debounceTime(400)
        .distinctUntilChanged()
        .subscribe(f => this.search(f))
  }

  search ({key, value}) {
    if (key === 'limit') {
      Object.assign(this.filter, {[key]: parseInt(value, 10)})
    }
    console.log(this.filter)
    this.getProductFiltred()
  }

  getProductFiltred() {
    this.filtered$ = this.productoService
      .getProductFiltred(this.filter)

    this.filtered$.subscribe(d => console.log(d))
  }

  loadProducts (id){
    this.router.navigate(['', 'user', 'productos', 'lista', 'lineas', id, 'productos'])
  }


  deleteLinea (id){
    this.linea = id

    this.lineas$.subscribe(
      ({lineas}) => {
          const i = lineas.findIndex(l => l._id === id)
          if (lineas[i].totalProductos > 0) {
            $('#myModal').modal('show')
          } else {
            this.deleteConfirmed()
          }
      })
  }

  deleteConfirmed() {
    $('#myModal').modal('hide')
    console.log('confirmed')

    this.productoService
      .deleteLinea(this.linea)
      .subscribe(
        (data) => this.lineas$ = this.productoService.getLineas(),
        (err) => console.log(err),
        () => console.log('Delete process ended.')
      )
  }

}



