import {Component, Input, ChangeDetectionStrategy} from '@angular/core'

@Component({
  moduleId: module.id,
  selector: 'presentacion-details',
  changeDetection: ChangeDetectionStrategy.OnPush,
  templateUrl: 'presentacion.html',
  styles: [`
    .nav.nav-tabs > li > a {
      text-transform: uppercase;
      font-family: 'Lato';
      font-weight: bold;
      color: #005395
    }
    .nav-tabs > li.active > a {
      color: #005395
    }

    .link {
      color: #8F8F8F
    }

    .link:hover {
      color: #005395;
      cursor: pointer
    }

    img {
      max-width: 235px;
      align-items: center;
      position: relative;
      left: 4.5em;
      padding: 1em;
      margin: 2em;
    }

    .table-info {
    }

  `]
})
export class PresentacionModalCmpt {
  @Input() presentacion$
}



