import { Component } from '@angular/core'
import {Router, ActivatedRoute, Params} from '@angular/router'
import { Observable } from 'rxjs/Observable'
import 'rxjs/add/operator/switchMap'

import {ProductoService, AuthService} from '../../../../services'

declare const $:any

@Component({
  moduleId: module.id,
  templateUrl: 'productos.component.html',
  styleUrls: ['productos.component.scss']
})
export class ProductosListaComponent {
  productos$: Observable<any>
  user$: Observable<any>

  linea: string
  producto: string

  constructor(
    private productoService: ProductoService,
    private authService: AuthService,
    private router: Router,
    private route: ActivatedRoute
  ){
    this.user$ = authService.USER
    this.getProductos()
  }

  getProductos () {
    this.productos$ = this.route.params
      .switchMap((params: Params) => {
        this.linea = params['linea']
        return this.productoService.getProductos(this.linea)
    })
  }

  loadPresentaciones (id){
    this.router.navigate(['', 'user', 'productos', 'lista', 'lineas', this.linea, 'productos', id, 'presentaciones'])
  }


  deleteProducto (id){
    this.producto = id

    this.productos$.subscribe(
      ({productos: {productos: p}}) => {
          const i = p.findIndex(c => c.id === id)
          if (p[i].totalPresentaciones > 0) {
            $('#myModal').modal('show')
          } else {
            this.deleteConfirmed()
          }
      })
  }

  deleteConfirmed() {
    $('#myModal').modal('hide')
    console.log('confirmed')

    this.productoService
      .deleteProducto(this.linea, this.producto)
      .subscribe(d => {
        console.log(d)
        this.getProductos()
      })
  }
}



